import { useState, useEffect } from "react";
import style from "./styles.module.css";
import ImageGalleryItem from "./ImageGalleryItem";
import Searchbar from "./Searchbar";
import PropTypes from "prop-types";
import ImagesApiService from "../apiService/ApiService";
import Loader from "react-loader-spinner";
import Modal from "./Modal";

const newImagesApiService = new ImagesApiService();

export default function ImageGallery() {
  const [imgArray, setImgArray] = useState([]);
  const [page, setPage] = useState(1);
  const [status, setStatus] = useState("idle");
  const [showModal, setShowModal] = useState(false);
  const [bigImg, setBigImg] = useState(null);
  const [searchImg, setSearchImg] = useState("");

  useEffect(() => {
    if (searchImg === "") { return;}
    setStatus("pending");
    setPage(1);
    newImagesApiService.query = searchImg;
    // console.log('searchImg',searchImg);
      newImagesApiService
        .searchImages()
        .then((data) => {
          if (data.hits.length > 0) {
            // console.log('data.hits', data.hits);
            setImgArray(data.hits);
            setStatus("success");
          } else {
            setStatus("error");
          }
        })
        .catch((error) => {
          setStatus("error");
        });
  }, [searchImg]);

  const onPageScroll = () => {
    window.scrollTo({
      top: document.documentElement.offsetHeight,
      behavior: "smooth",
    });
  };

  const handleClick = () => {
    newImagesApiService.pages = 1;
    newImagesApiService
      .searchImages()
      .then((data) => {
        setImgArray((prev)=>[...prev, ...data.hits]);
        setPage(newImagesApiService.pages);
        setStatus("success");
        onPageScroll();
      })
      .catch((error) => {
        setStatus("error");
      });
  };

  const toggleModal = () => { setShowModal(!showModal) };

  const onGalleryImageClick = (e) => {
    e.preventDefault();
    let imgSrc = e.target.src;
    setBigImg(imgArray.find((el) => el.webformatURL === imgSrc));
    // toggleModal();
    setShowModal(!showModal);
  };

  const onSubmit = (data) => {
    setSearchImg(data)
  };

  return <>
    <Searchbar onSubmit={onSubmit} />
    {(status === "idle") && <p className={style.text}>Hello! Type some searching query</p>}
    {(status === "pending")&& <Loader
          type="Circles"
          className={style.loading}
          color="#00BFFF"
          height={100}
          width={100}
          timeout={7000} //3 secs
    />}
    {(status === "success") &&
      <>
          <ul className={style.ImageGallery}>
            <ImageGalleryItem
              imgArray={imgArray}
              onGalleryImageClick={onGalleryImageClick}
            />
          </ul>
          <button
            type="button"
            onClick={handleClick}
            className={style.Button}
          >
            Load more
          </button>
          {showModal && (
            <Modal toggleModal={toggleModal} bigImg={bigImg}></Modal>
          )}
      </>}
    {(status === "error")&& <p className={style.text}>Ooops! Something went wrong</p>}
    </>
}

